@import "colors.css";

h2{
  margin-top:0px;
}
.Skins{
  margin: 0px auto;
  padding: 0px;
  width:110vw;
  height:100vh;
  background-color: var(--background-color);
  overflow:scroll;
  scrollbar-width: none;
}

.Skins-Back{
  position: absolute;
  top: 150vh;
  left: 201vw;
}
.Skins-Title{
  text-align:center;
  border-bottom:solid 1.5px  var(--decorate-color);
  width:92%;
  margin-bottom:20px;
  padding-bottom:20px;
  margin-top:-30px;

}
.Skins-Title-Text{
  margin-top:0px;
  color:var(--section-title);
  font-size:35px;
  font-family: cs-font;
}
.Search-input{
  text-align:center;
  width:92%;
}
.Search-input input{
  background: none;
  border-radius: 10px;
  padding:5px 15px 5px 15px;
  color:  var(--sec-font-color);
  border:none;
  border: solid 1px var(--select-border-color);
}
.Select-valute {
width:92%;
padding-top:10px;
margin-top:20px;
}

.Select-valute  .Valute-Choise{
  margin-right:20px;
  border-radius:5px;
  width:60px;
  height:20px;
  box-shadow:none;
  border: solid 1px var(--select-border-color);
  outline:none;
  box-shadow: 3px 3px 5px 2px black;
}
.Skins-LogoCS-IMG{
  width:40%;
  height:20%;
  margin-left:50%;
  transform:translate(-65%);
  }

.Skins-Body{
  height:100%;
}
.One-Skin{
  width:92%;
  overflow:auto;
  display:block;
  text-align:center;
  border-bottom:solid 1.5px var(--decorate-color);
  padding-bottom: 20px;
  padding-left: 20px;
  margin-bottom:30px;
}
.One-Skin-Name{
  font-size:40px;
  text-align:center;
  margin: 20px 0px 20px 0px;
  font-family: "Arial Black";
}

.Skins-img_float{
  width: 45%;
  float: left;
  display:inline;
  text-align:center;
  margin-left:2%;
}

.Skins-img{
  display:block;
  margin:0px auto;
}

.Skins-cost_float{
width:45%;
float:right;
display:inline;
margin-right:4%;
box-shadow: 8px 6px 12px -2px black;
margin-top:35px;
}

.Skins-cost_float p {
text-align:left;
margin-left:5%;
margin:0px;
padding:10px;
}

.price_skin{
  margin-left:10px;
  float:right;
}
.Skins-cost-shadow{
  background:var(--list-sec-color);
}

.Skin-float-bt{
  background:none;
  border:none;
  overflow:hidden; 
  color:var(--sec-font-color);
  cursor:pointer;
  outline:none;
  font-size:15px;
  height:30px;
  transition-duration: 1s;
}



/* ZUZYCIE */
.fn-button{
  background:green;
}

.fn-button span{
  margin-bottom:-10px;
}

.fn-button:hover{
  background:rgb(0, 39, 0);
  box-shadow: 0 8px 5px -2px black;
}

.mw-button{
  background:rgb(83, 128, 0);
}
.mw-button:hover{
  background:rgb(34, 53, 0);
  box-shadow: 0 8px 5px -2px black;
}

.ft-button{
  background:rgb(128, 126, 0);
}
.ft-button:hover{
  background:rgb(58, 56, 0);
  box-shadow: 0 8px 5px -2px black;
}

.ww-button{
  background:rgb(128, 62, 0);
}
.ww-button:hover{
  background:rgb(61, 30, 0);
  box-shadow: 0 8px 5px -2px black;
}
.bs-button{
  background:rgb(128, 0, 0);
  
}
.bs-button:hover{
  background:rgb(37, 0, 0);
  box-shadow: 0 8px 5px -2px black;
}



.Form-Search{
  width:300px;
  margin-left:10%;
  margin-left: 50%;
  transform: translate(-50%);
}
.Search-Skin-input{
  box-sizing: border-box;
  width: 300px;
  height: calc(3em + 2px);
  margin: 0 0 1em;
  padding: 1em;
  border: 1px solid var(--list-sec-color);
  border-radius: 1.5em;

  resize: none;
  outline: none;
  box-shadow:  3px 3px 5px 2px black;
}
.Search-input-label{
  margin-left:-5px;
  float:left;
}
.Search-Skin-input[required]:focus {
  border-color: var(--decorate-color);
}
.Search-Skin-input[required]:focus + .Search-input-label[placeholder]:before {
  color: var(--decorate-color);
}

.Search-Skin-input[required]:focus + .Search-input-label[placeholder]:before,
.Search-Skin-input[required]:valid + .Search-input-label[placeholder]:before {
  transition-duration: .2s;
  transform: translate(0,-1.0em) scale(.9,.9);
}
.Search-Skin-input[required]:invalid + .Search-input-label[placeholder][alt]:before {
  content: attr(alt);
}
.Search-Skin-input[required] + .Search-input-label[placeholder] {
  display: block;
  pointer-events: none;
  line-height: 1.25em;
  margin-top: calc(-2.3em - 2px);
  margin-bottom: calc((3em - 1em) + 2px);
}
.Search-Skin-input[required] + .Search-input-label[placeholder]:before {
  content: attr(placeholder);
  display: inline-block;
  margin: 0 calc(1em + 2px);
  padding: 0 2px;
  color:  var(--list-sec-color);
  white-space: nowrap;
  transition: .3s ease-in-out;
  background-size: 100% 5px;
  background-repeat: no-repeat;
  background-position: center;
  background:var(--background-color);
  border:radius 5px;
}
#Skins-Valute{
  margin-top:-10px;
}

.Stat-track{
  color:rgb(255, 115, 0);
}


/* TABLET */

@media only screen and (max-width:1199px){
  .Skins{
    width:100vw;  
    height: 100vh;
    overflow:hidden;
    margin-left: -5.1vw;
  }
  .Skins-Title{
    width:100%; 
    height: 20%;
    margin-bottom: 0px;
  }
  .Skins-Back{
    top:104vh;
    left:292vw;
  }
  .Select-valute{
    margin-left:-80%;
  }
  .Skins-Body{
  font-size:17px;
  overflow:scroll;
  scrollbar-width: none;
  overflow-x:hidden;
  width:100%;
  margin:0px auto;
  height:70%;
  
  }
  .Skins-Body::-webkit-scrollbar{
   display:none;
  }
  
  
  .One-Skin{
    width:100%;
    margin-left:0px;
  }
  .Skins-img_float{
    width:100%;
    margin-left:-20px;
  }
  .Skins-img {
    width:40%;
  }
.One-Skin-Name{
  width:100%;
  margin:0px auto;
  margin-top:15px;
}
.Skins-cost_float{
  width:100%;
  margin-right: 20px;
}
.Skins-LogoCS-IMG{
  height:26%;
}
}

/* PHONE */

@media only screen and (max-width:799px){
  .Skins{
    margin-left: -5vw;
    
  }
  .Form-Search{
    margin-left:50%;
  }
  
  .Skins-Back{
    left:282vw;
  }
  .Select-valute{
    margin-left:0px;
  }
  .Skins-Body{
  width:98%;
  }
  .Skins-img {
    width:80%;
  }
.One-Skin-Name{
  width:90%;
}
.Skins-LogoCS-IMG{
  height:20%;
}
}


